import React, { useState } from "react";
// import "./contact.css";
import "./contact.scss";

const Contact = () => {
  const [formData, setFormData] = useState({
    nombre: "",
    email: "",
    description: "",
  });

  const [enviado, setEnviado] = useState(false);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // Simular un envo de formulario (puedes reemplazar esto con tu lgica real de envo)
    // Aqu podras enviar los datos al servidor o realizar cualquier otra accin necesaria.

    // Mostrar el mensaje "Enviado"
    setEnviado(true);
    console.log("Enviado:", enviado);

    // Ocultar el mensaje "Enviado" despus de 3 segundos (ajusta este valor segn tus necesidades)
    setTimeout(() => {
      setEnviado(false);
      console.log("Enviado:", enviado); // Asegrate de que cambie a false despus del tiempo especificado
    }, 3000);
  };

  return (
    <section id="contact">
      <div className="container-lg contact">
        <div className="section-header">
          <h4>Contacto</h4>
          <p>Nos tomamos un caf?</p>
        </div>
        <div className="section-container">
          <div className="contact-form">
            <form onSubmit={handleSubmit}>
              <div className="container-contact">
                <div className="contact-left">
                  <label htmlFor="nombre">Nombre</label>
                  <input
                    type="text"
                    id="nombre"
                    name="nombre"
                    value={formData.nombre}
                    onChange={handleInputChange}
                    required
                  />

                  <label htmlFor="email">Email</label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    required
                  />
                </div>
                <div className="contact-right">
                  <label htmlFor="desecription">Descripcin</label>
                  <textarea
                    id="description"
                    name="description"
                    value={formData.description}
                    style={{ resize: "none" }}
                    rows={"7"}
                    onChange={handleInputChange}
                    required
                  />
                </div>
              </div>
              <input className="bt-submit" type="submit" value="Enviar" />
            </form>
          </div>
        </div>
        {enviado && <div className="enviado-message">Enviado</div>}
      </div>
    </section>
  );
};

export default Contact;
